import React, { useEffect, useState } from "react";
import { Button, Card, Col, Form, Row } from "react-bootstrap";
import { MdOutlineDeleteForever } from "react-icons/md";
import { FaPlus } from "react-icons/fa6";
import { AiOutlineClose } from "react-icons/ai";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { BiEdit } from "react-icons/bi";
import { useNavigate } from "react-router-dom";

import { PATH } from "../../../../utils/path";
import { ANSWER_TYPE } from "../../../../components/LookupData/Data";
import {
  createAdminPresForm,
  getAdminPresForm,
  getAllAdminPresForm,
} from "../../../../app/features/OnlinePrescriptionForm/OnlinePrescriptionForm.slice";

function Questionnaires() {
  const { handleSubmit, register } = useForm();
  const [selectedType, setSelectedType] = useState("401");
  const [sections, setSections] = useState([
    { question: "", options: [""], selectedType: "401" },
  ]);
  const [editing, setEditing] = useState(false);

  const { adminOnlinePrescripionForm } = useSelector(
    (state) => state?.onlinePrescriptionForm
  );
  console.log("adminOnlinePrescripionForm", adminOnlinePrescripionForm);
  console.log("sections", sections);

  const navigate = useNavigate();
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("family_doc_app"));

  const searchParams = new URLSearchParams(window.location.search);
  let paramsData = searchParams.get("disease");

  const handleTypeChange = (e, index) => {
    setSelectedType(e.target.value);
    const updatedSections = [...sections];
    updatedSections[index].selectedType = e.target.value;
    setSections(updatedSections);
  };

  const handleAddSection = () => {
    setSections([
      ...sections,
      { question: "", options: [""], selectedType: "401" },
    ]);
  };

  const handleQuestionChange = (sectionIndex, value) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex].question = value;
    setSections(updatedSections);
  };

  const handleAddOption = (sectionIndex) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex].options = [
      ...sections[sectionIndex].options,
      "",
    ];
    setSections(updatedSections);
  };

  const handleOptionChange = (sectionIndex, optionIndex, value) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex].options[optionIndex] = value;
    setSections(updatedSections);
  };

  const handleCloseOption = (sectionIndex, optionIndex) => {
    const updatedSections = [...sections];
    updatedSections[sectionIndex].options.splice(optionIndex, 1);
    setSections(updatedSections);
  };

  const handleDeleteSection = (index) => {
    const updatedSections = [...sections];
    updatedSections.splice(index, 1);
    setSections(updatedSections);
  };

  const handleSwitchChange = (sectionIndex, isChecked) => {
    console.log(`Switch in section ${sectionIndex} is now ${isChecked}`);
  };

  useEffect(() => {
    const finalData = {
      formId: paramsData,
    };
    dispatch(getAdminPresForm(finalData));
  }, [dispatch, paramsData]);

  useEffect(() => {
    if (paramsData && adminOnlinePrescripionForm) {
      setSections([]);
      adminOnlinePrescripionForm?.data?.formQuestionList?.map(
        (section, sectionIndex) => {
          const newSection = {
            question: section.question,
            options: section.formAnswersList,
            selectedType: section.answerTypeId.toString(),
          };
          return setSections((prevSections) => [...prevSections, newSection]);
        }
      );
    }
  }, [paramsData, adminOnlinePrescripionForm]);

  function onCreateSuccess() {
    if (user?.roleId === 1) {
      navigate(PATH.SUPERADMIN_FORMS);
    } else if (user?.roleId === 4) {
      navigate(PATH.ADMIN_FORMS);
    }

    let finalData = {
      Search: "",
    };
    dispatch(getAllAdminPresForm(finalData));
  }

  function createOnlinePresForm(submittedData) {
    const finalData = {
      formId: 0,
      title: submittedData?.title,
      isActive: true,
      formQuestionList: sections.map((value, index) => ({
        questionId: 0,
        question: value?.question,
        answerTypeId: value?.selectedType,
        answerTypeName:
          value?.selectedType === "401"
            ? "Radio"
            : value?.selectedType === "402"
            ? "Checkbox"
            : value?.selectedType === "404"
            ? "TextBox"
            : "",
        formId: 0,
        formAnswersList: value.options.map((value, index) => ({
          formAnswereId: 0,
          formQuestionId: 0,
          answerLabel: value ? value : "Answer Text",
        })),
      })),
    };
    dispatch(createAdminPresForm({ finalData, onCreateSuccess }));
  }

  return (
    <>
      <h5>Questionnaires</h5>
      <Card className="shadow-sm custom-questionnaires">
        <Form onSubmit={handleSubmit(createOnlinePresForm)}>
          <Row className="m-3 d-flex justify-content-center flex-column">
            <Col xl={7} lg={10} xs={12} className="mx-auto mb-3 p-0">
              <Form.Group
                className="px-3 pt-2 pb-4 rounded mb-3"
                controlId="formTitle"
                style={{
                  boxShadow: "0px 0px 24px 0px #0000000A",
                  borderTop: "6px solid #6045EB",
                }}
              >
                <Form.Label className="mt-2">Enter Form Title</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Form Title"
                  maxLength={250}
                  {...register("title", { required: true })}
                  disabled={paramsData && !editing}
                  defaultValue={
                    paramsData ? adminOnlinePrescripionForm?.data?.title : ""
                  }
                />
              </Form.Group>
              {sections?.length === 0 ? (
                <Button
                  variant="primary"
                  type="button"
                  className="Admin-Add-btn px-3 float-end"
                >
                  <FaPlus size={24} onClick={handleAddSection} />
                </Button>
              ) : (paramsData && !editing) ? (
                <Button
                  variant="primary"
                  type="button"
                  className="Admin-Add-btn px-3 float-end"
                  onClick={() => {
                    setEditing(true);
                  }}
                >
                  <BiEdit size={24} />{" "}
                  Edit
                </Button>
              ) : (
                ""
              )}
            </Col>
            {sections.map((section, sectionIndex) => (
              <Col
                key={sectionIndex}
                xl={7}
                lg={10}
                xs={12}
                className="p-3 rounded mx-auto mb-3"
                style={{
                  boxShadow: "0px 0px 24px 0px #0000000A",
                  borderLeft: "6px solid #3F8BFC",
                }}
              >
                <Form.Group
                  className="mb-3"
                  controlId={`formQuestion${sectionIndex}`}
                >
                  <div className="d-flex justify-content-between flex-wrap align-items-center custom-col-rev mb-2">
                    <Form.Label className="text-w-full">
                      Enter Question
                    </Form.Label>
                    <Col xl={5} sm={6} xs={12}>
                      <Form.Select
                        aria-label="Select Type"
                        onChange={(e) => {
                          handleTypeChange(e, sectionIndex);
                        }}
                        defaultValue={section?.selectedType || selectedType}
                      >
                        {ANSWER_TYPE.map((type) => {
                          return (
                            <option value={type?.lookupId}>{type.name}</option>
                          );
                        })}
                      </Form.Select>
                    </Col>
                  </div>
                  <Form.Control
                    type="text"
                    placeholder="Question"
                    required={true}
                    maxLength={250}
                    value={section?.question} // Set the value of the input
                    onChange={(e) =>
                      handleQuestionChange(sectionIndex, e.target.value)
                    }
                  />
                  <div className="mt-2 questionnaires-option">
                    {section?.selectedType === "401" && (
                      <>
                        {section.options.map((option, optionIndex) => (
                          <div
                            className="d-flex align-items-center"
                            key={`radio-${optionIndex}`}
                          >
                            <Form.Check
                              label={
                                <>
                                  <Form.Control
                                    type="text"
                                    placeholder="Add option"
                                    className="border-0 ms-2"
                                    value={option}
                                    onChange={(e) =>
                                      handleOptionChange(
                                        sectionIndex,
                                        optionIndex,
                                        e.target.value
                                      )
                                    }
                                  />
                                </>
                              }
                              name={`group${sectionIndex}`}
                              type="radio"
                              id={`radio-${optionIndex}`}
                              className="d-flex align-items-center mb-1 w-100"
                            />
                            <span className="mx-3">
                              <AiOutlineClose
                                size={18}
                                onClick={() =>
                                  handleCloseOption(sectionIndex, optionIndex)
                                }
                              />
                            </span>
                          </div>
                        ))}
                        <span
                          className="text-cursor-pointer text-decoration-underline"
                          style={{ color: "#3F8BFC", fontSize: "0.9em" }}
                          onClick={() => handleAddOption(sectionIndex)}
                        >
                          Add another option
                        </span>
                      </>
                    )}
                    {section?.selectedType === "404" && (
                      <>
                        <Form.Control
                          as="textarea"
                          rows={3}
                          placeholder="Answer Text"
                          className="mt-2"
                          disabled={true}
                        />
                      </>
                    )}
                    {section?.selectedType === "402" && (
                      <>
                        {section.options.map((option, optionIndex) => (
                          <div
                            key={`checkbox-${optionIndex}`}
                            className="d-flex align-items-center"
                          >
                            <Form.Check
                              label={
                                <>
                                  <Form.Control
                                    type="text"
                                    placeholder="Add option"
                                    className="border-0 ms-2"
                                    value={option}
                                    onChange={(e) =>
                                      handleOptionChange(
                                        sectionIndex,
                                        optionIndex,
                                        e.target.value
                                      )
                                    }
                                  />
                                </>
                              }
                              name={`group${sectionIndex}`}
                              type="checkbox"
                              id={`checkbox-${optionIndex}`}
                              className="d-flex align-items-center mb-1 w-100"
                            />
                            <span className="mx-3">
                              <AiOutlineClose
                                size={18}
                                onClick={() =>
                                  handleCloseOption(sectionIndex, optionIndex)
                                }
                              />
                            </span>
                          </div>
                        ))}
                        <span
                          className="text-cursor-pointer text-decoration-underline"
                          style={{ color: "#3F8BFC", fontSize: "0.9em" }}
                          onClick={() => handleAddOption(sectionIndex)}
                        >
                          Add another option
                        </span>
                      </>
                    )}
                  </div>
                </Form.Group>
                <div className="w-100 d-flex flex-wrap justify-content-end">
                  <span style={{ borderRight: "1px solid #CACACA" }}>
                    <FaPlus
                      size={24}
                      color="#CACACA"
                      className="mx-2 text-cursor-pointer"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Add Question"
                      onClick={handleAddSection}
                    />
                  </span>
                  <span
                    className="me-2"
                    style={{ borderRight: "1px solid #CACACA" }}
                  >
                    <MdOutlineDeleteForever
                      size={26}
                      color="#CACACA"
                      className="mx-2 text-cursor-pointer"
                      data-toggle="tooltip"
                      data-placement="top"
                      title="Delete"
                      onClick={() => handleDeleteSection(sectionIndex)}
                    />
                  </span>
                  <Form.Check
                    type="switch"
                    id={`custom-switch-${sectionIndex}`}
                    label="Active"
                    {...register("isActive")}
                    defaultChecked
                    onChange={(e) => {
                      handleSwitchChange(sectionIndex, e.target.checked);
                    }}
                  />
                </div>
              </Col>
            ))}
            {sections?.length !== 0 && (
              <Col>
                <Button
                  variant="primary"
                  type="submit"
                  className="Admin-Add-btn fw-bold float-end"
                >
                  Save
                </Button>
              </Col>
            )}
          </Row>
        </Form>
      </Card>
    </>
  );
}

export default Questionnaires;
